import { klona } from 'klona';
/**
 * Zod Schema 工具函数和工厂函数
 */
// 基础 Transform 工具函数
/** 数组去重 transform */
export const uniqueArray = (arr) => _.uniq(arr);
/** 数组去重并强制包含特定值 */
export const uniqueArrayWith = (required) => (arr) => _.uniq([...arr, ...required]);
/** 数值范围限制 transform */
export const clamp = (min, max) => (val) => _.clamp(val, min, max);
/** 锁定值 transform（无论输入什么都返回固定值） */
export const locked = (value) => () => value;
// Schema 工厂函数
/**
 * 物品对象 schema
 */
export const itemSchema = z.object({
    desc: z.string(),
    type: z.string(),
    quantity: z.coerce.number(),
});
/**
 * 带默认值恢复的数量 schema
 * 当 quantity 为 0 时自动恢复到默认值，而不是被删除
 * @param defaultVal - 默认数量值
 * @example
 * quantity: quantityWithDefault(2) // 当输入为 0 时恢复为 2
 */
export const quantityWithDefault = (defaultVal) => z.coerce
    .number()
    .prefault(defaultVal)
    .transform(val => (val <= 0 ? defaultVal : val));
/**
 * 过滤掉 quantity 为 0 的物品 transform 函数
 * 用于物品容器的后处理，移除数量为 0 的物品
 * 注意：固定物品数量应使用 quantityWithDefault 来在 0 时恢复默认值，这样不会被此函数删除
 * @example
 * z.object({ ... }).catchall(itemSchema).prefault({}).transform(filterZeroQuantityItems)
 */
export const filterZeroQuantityItems = (items) => _.pickBy(items, item => {
    // 检查 item 是否为对象且包含 quantity 属性
    if (item && typeof item === 'object' && 'quantity' in item) {
        const quantity = item.quantity;
        // 保留 quantity 不存在、undefined 或大于 0 的物品
        return quantity === undefined || quantity > 0;
    }
    // 非物品对象直接保留
    return true;
});
/**
 * 锁定字符串 schema（值不可变）
 */
export const lockedStr = (value) => z.string().prefault(value).transform(locked(value));
/**
 * 锁定物品 schema（值不可变，无论输入什么都返回默认值）
 */
export const lockedItem = (defaultDesc, defaultType, defaultQuantity = 1) => z
    .object({
    desc: lockedStr(defaultDesc),
    type: lockedStr(defaultType),
    quantity: z.coerce.number().prefault(defaultQuantity).transform(locked(defaultQuantity)),
})
    .prefault({});
/**
 * 外观记录 schema
 */
export const appearance = (defaults = {}) => z.record(z.string(), z.string()).prefault(() => klona(defaults));
/**
 * 唯一字符串数组 schema（带去重）
 */
export const uniqueStrArray = (defaults = []) => z
    .array(z.string())
    .prefault(() => klona(defaults))
    .transform(uniqueArray);
/**
 * 唯一字符串数组 schema（带去重和强制包含值）
 */
export const uniqueStrArrayWith = (defaults, required) => z
    .array(z.string())
    .prefault(() => klona(defaults))
    .transform(uniqueArrayWith(required));
/**
 * 范围限制数值 schema
 */
export const clampedNum = (defaultVal, min, max) => z.coerce.number().prefault(defaultVal).transform(clamp(min, max));
/**
 * 灵魂对象 schema
 */
export const soulSchema = z
    .object({
    desc: z.string().prefault(''),
    灵魂能力: z.record(z.string(), z.string()).prefault({}),
})
    .prefault(() => ({ desc: '', 灵魂能力: {} }));
